import axios from "axios";

import type { GithubAccount } from "../models/githubAccount";

type EventType =
	| "CommitCommentEvent"
	| "CreateEvent"
	| "DeleteEvent"
	| "GollumEvent"
	| "ForkEvent"
	| "IssuesEvent"
	| "MemberEvent"
	| "IssueCommentEvent"
	| "MemberEvent"
	| "PullRequestEvent"
	| "PullRequestReviewEvent"
	| "PullRequestReviewCommentEvent"
	| "PushEvent"
	| "ReleaseEvent"
	| "SponsorshipEvent"
	| "WatchEvent";

export interface GithubEvent {
	id: number;
	type: string | EventType;
	actor: {
		id: number;
		login: string;
		display_login: string;
		gravatar_id: string;
		url: string;
		avatar_url: string;
	};
	repo: {
		id: number;
		name: string;
		url: string;
	};
	payload: any;
	created_at?: Date;
	public?: boolean;
}

export const getUser = async (git_username: string): Promise<GithubAccount> => {
	try {
		const { data } = await axios.get(
			`https://api.github.com/users/${git_username}`
		);
		return data;
	} catch (error) {
		console.error(error);
		throw new Error(error);
	}
};

export const getActivity = async (
	git_username: string
): Promise<GithubEvent[]> => {
	try {
		const { data } = await axios.get(
			`https://api.github.com/users/${git_username}/events`
		);
		return data;
	} catch (error) {
		console.error(error);
		throw new Error(error);
	}
};
